import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
	providedIn: 'root'
})

export class GetsrcdestService {
	private url = "/getsrcdest";

	constructor(private http: HttpClient) { }

	getsrcdest() {
		return this.http.get<any>(this.url);
	}

}