import { Component, OnInit, Input } from '@angular/core';
import { CreateDatasourceService } from '../../../services/create-datasource.service';
import { Router } from '@angular/router';
import { FordownloadService } from '../../../services/fordownload.service';

@Component({
	selector: 'uapi-connections-scheduler',
	templateUrl: './connections-scheduler.component.html',
	styleUrls: ['./connections-scheduler.component.css']
})
export class ConnectionsSchedulerComponent implements OnInit {
	@Input() filteredEndpoints;
	@Input() selected;
	private scheduler = false;
	private period = 30 * 60;
	private destination;
	private download;
	private endpoint;
	private downloadData;
	private loaderShow = false;
	constructor(private createdatasourceService: CreateDatasourceService,
		private fordownloadService: FordownloadService, private router: Router) { }

	ngOnInit() {
	}

	schedulerSwitch() {
		this.scheduler = !this.scheduler;
	}

	onSelectChange(value) {
		this.period = value;
	}


	createDatasource() {
		console.log(this.filteredEndpoints + "  " + this.scheduler + "  " + this.period);
		this.loaderShow = true;
		this.createdatasourceService.createdatasource("export", this.filteredEndpoints, this.scheduler, this.period, this.selected.selectedDestination.id)
			.subscribe(
				data => {
					console.log(data);
						this.router.navigate(['/datasources/'+data.status]);
				}
			);
	}

	downloadDatasource(choice) {
		this.destination = choice;
		this.endpoint = JSON.stringify(this.filteredEndpoints);
		this.download = true;
		this.loaderShow = true;
		this.createdatasourceService.createdatasource("download", this.filteredEndpoints, this.scheduler, this.period, this.selected.selectedDestination.id)
			.subscribe(
				data => {
					console.log(data);
					if (data.status == 200) {
						this.downloadData = data.data.data;
						this.loaderShow = false;
					}
				}
			);
	}

	closeDownload() {
		this.download = false;
	}
}
